
/**
 * Breadth First Search (BFS) algorithm for traversing & searching tree data structure of DOMexplores the neighbor nodes first, before moving to the next level neighbors.Time complexity: between O(1) and O(|V|^2).
 */
export class BFS {
/**
 * Target node is a non empty text node.
 * @param {?} rootNode
 * @return {?}
 */
public getTargetNode(rootNode: any): any {
        return this.walk(rootNode);
    }
/**
 * @param {?} rootNode
 * @return {?}
 */
private walk(rootNode: any): any {
        let /** @type {?} */ queue: any[] = [];
        let /** @type {?} */ iNode: any;

        queue.push(rootNode);

        while (queue.length > 0) {
            iNode = queue.shift();
            if (this.isTargetNode(iNode)) {
                return iNode;
            }
            for (let /** @type {?} */ child of iNode.childNodes) {
                queue.push(child);
            }
        }
        return null;
    }
/**
 * @param {?} node
 * @return {?}
 */
private isTargetNode(node: any): boolean {
        return typeof node !== "undefined" &&
            node.nodeValue != null &&
            node.nodeValue.trim() != "" &&
            node.nodeType == 3;
    }

}

